///////////////////////////////////////////////////////
/// ABOUT
//////////////////////////////////////////////////////

.block {
    display: block;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 30px;
    box-shadow: 0 3px 35px rgba(0, 0, 0, .1);
}

// sizes the images with responsive-image class to 100% of its container width 
.responsive-image {
    max-width: 100%;
    height: auto;
}
.about-title{
    font-size: 2.25rem;
}

// ==========================================================================
// welcome-to-netmatters Block
// ==========================================================================

// This adds the top and bottom padding of 50px, made so it can be used on other parts if needed. 

// setting the brand-backgrounds color
.welcome-to-netmatters{
    background-color: $primary-background;
    color: $primary-white;

    // sets styling for buttons in welcome to net matters section
    .btn, btn-primary-inverse {
        border-radius: 6px;
        padding: 10px 30px;
        font-size: 20px;
        border: 1px solid transparent;
    }
}

.row-1 .btn{
    background-color: #272936;
}

.icon-star-full{
    color: #ffa500;
    margin-bottom: 25px;
    font-size: 50px;
}

.btn-google{
    background-color: #4387f6;
}

.btn-trustpilot{
    background-color: #00b67b;
}

.icon-arrow-right2:before {
    content: "\ea3c";
    }

.welcome-to-netmatters a{ // this takes away the underlines of the anchors and applies white
    text-decoration: none;
    color: $primary-white;

    &:hover {
        text-decoration: underline; /* Adds underline on hover */
    }
}

// this styles the client qoute in the second section of about
.row-2 {
    .client-quote{
        font-size: 1.3rem;
        font-weight: 600;
        font-style: italic;
    }
}

.btn{
    font-weight: 400;
    text-decoration: none;
    color: $primary-white;

    height: auto;
    line-height: 35px;
    padding: 5px 15px;
    text-transform: uppercase;
    font-size: 1.063rem;
    border-radius: 3px;
    white-space: normal;

    .main-buttons{
        padding: 10px 30px;
    }
}

