// }

// ==========================================================================
// Latest-news-block
// ==========================================================================


.latest-news-block{
    padding-bottom: 30px;
}

.article-padding{
    padding-top: 30px;
}

// this sets the img-container to relative so the .news-link can then be positioned absolutley
.img-container{
    position: relative;
}

//styles the latest news h1
.latest-news-h1{
    color: #333645;
    font-weight: 600;
    margin: 0px;
}

// colors the view all --> in latest news
.latest-news-view-all{
    color: #333645;
    font-size: 1.625rem;
    font-weight: 600;
    text-decoration: none;
}

// this is what is aligning the latest news and view all -->
.heading-container {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: space-between;
    
}

// as this is set to absolute, its positioned relative to img-container as thats its ancestor,
// the top: 10px is self explanitory, and the right: 0 means it will be as far right as possible.
.news-link{
    color: $primary-white;
    position: absolute;
    text-decoration: none;
    font-size: .75rem;
    padding: 0px 10px;
    right: 0;
    top: 10px;
    z-index: 1;

}
// this is a secondary class to the .news-link so it has a unique color
.news-link-one{
    background-color: #4183d7;
}
    // this is a secondary class to the .new-link so it has a unique color
.news-link-two{
    background-color: #db8c0d;
}

// This targets the h3 in content section to change its color and style and the first btn to make it blue
.content-section {
    .article-1,
    .article-2,
    .article-3 {
        .block {
            h3 {
                font-size: 1rem;
                line-height: 1.5rem;
                // color: #4183d7;
                a {
                    color: inherit;
                    text-decoration: none;
                }
            }
            p{
                line-height: 1.375rem;
                font-size: .8125rem;
            }
        }
    }

}
// this block of selectors lets me set the titles colors individually
.block {
    .btn-article-1{
        background-color: #4183d7;
    }
    .btn-article-2{
        background-color: #db8c0d;
    }
    .btn-article-3{
        background-color: #d64541;
    }
    .article-1-h3{
        color: #4183d7;
    }
    .article-2-h3{
        color: #db8c0d;
        .time-to-read{
            color: #adaeae;
            font-size: .85rem;
        }
    }
    .article-3-h3{
        color :#d64541;
    }
}

////////////////////////////////////////////////////////
// HOVER SHIFT -
///////////////////////////////////////////////////////

.article-1,
.article-2,
.article-3{
    @include hover-move;
}

/////////////////////////////////////////////////////////////////////


// Article-1
// ==========================================================================



.bar{
    padding-top: 50px;
}

// These are for the first articles text and picture styles.
.author {
    padding-top: 30px;
    margin-top: 30px;
}

// These 4 selectors seperate the image and text
.clearfix::after {
    content: "";
    display: table;
    clear: both;
}
.author-img {
    float: left;
    margin-right: 10px;
}
.author-img img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.author-details {
    overflow: hidden;
    padding-left: 20px;
}

// Article-2
// ==========================================================================

//  View all block
// ==========================================================================

// setting the font size and pushing the content down 20px's and to the center
.view-all{
    margin-top: 50px;
    text-align: center;
    a{
        font-size: 1.625rem;
    }
}

//  BOTTOM CLIENT BAR
// ==========================================================================

.bottom-clients-bar{
    padding-top: 50px;
    padding-bottom: 50px;
}

.bottom-bar-images{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

/////////////////////////////////////////////////////////////////////////////
// Media queris 
/////////////////////////////////////////////////////////////////////////////

@media (max-width: 767px) {
    
    // sizes the image to 100% width whilst 768px's and under
    .responsive-image {
        width: 100%;
        max-width: none;
        margin-left: 50%;
        transform: translateX(-50%);
    }
    // this makes the view all in the latest news block disappear under 268px's
    .latest-news-block .latest-news-view-all {
        display: none; /* Increase specificity */
    }
}

@media (min-width: 768px){

    //////////////////////////////////////////////////////////////////////////////////
    /// LATEST NEWS
    /////////////////////////////////////////////////////////////////////////////////

    .latest-news-block {
        padding-top: 15px;
        padding-bottom: 50px;
        width: 100%
    }

    .row{
        display: flex;

        // this sets article 1 to appear first and gives padding to seperate the articles.
        .article-1{
            width: 50%;
            padding-left: 15px;
            order: 1;
        }

        .article-2{
            width: 50%;
            padding-right: 15px;
        }

        // this hide the third article
        .article-3{
            display: none;
        }

    }    
    // this gets rid of the view all and -->
    .container .view-all {
        display: none;
    }
}


